.header{
    display: flex;
    justify-content: space-between;
    border: 1px solid black;
}
.logo{
    width: 130px;
}

.nav-items{
    padding: 20px;
}

.nav-items > ul{
    font-size: 19px;
    display: flex;
    list-style-type: none;
    padding: 0px 20px;
}
.nav-items > ul > li {
    padding: 10px;
    margin: 10px;
}
.res-container{
    display: flex;
    flex-wrap: wrap;
    
}

.res-card{
    margin: 4px;
    padding: 5px;
    width: 200px;
   height: 400px;
    
}
.res-card:hover{
    border: 1px solid black;
    cursor: pointer;
}
.search{
    padding: 10px;
}

.res-logo{
   
    width: 100%;
}

.filter-btn{
    margin: 12px;
    cursor: pointer;
}
